import React from 'react';
import Swiper from 'react-id-swiper';

import wxstyles from 'app/common/css/weui.min.css'
import styles from 'app/common/css/style.css'
import loginstyles from 'app/common/css/login.css'

const Login = React.createClass({
    render() {
			var params = {
			pagination: '.swiper-pagination',
			paginationClickable: true,
			nextButton: '.swiper-button-next',
			prevButton: '.swiper-button-prev',
			spaceBetween: 30
			};

        var bodystyle = {
        	        position: "relative",
                    height: "100%",
                    fontFamily: "Helvetica Neue, Helvetica, Arial, sans-serif",
                    fontSize: "14px",
                    color:"#000",
                    margin: 0,
                    padding: 0                    
        }

			return (
				    <div style={bodystyle}>
					<div className={loginstyles.wrapper}>
					<img src={require('app/common/images/font_logo.png')} alt="" style={{ margin:"0 auto", paddingTop: "39px", paddingBottom: "44px"}}/>
					</div>    
                    <Swiper
								pagination={params.pagination}
								paginationClickable={params.paginationClickable}
								nextButton={params.nextButton}
								prevButton={params.prevButton}
								spaceBetween={params.spaceBetween}>
								<div className="swiper-slide">
								<div className="swiper-wrap">
								<div className={loginstyles.text-input-box+' '+loginstyles.Mtop40}>
								<input type="tel" className={loginstyles.inputText} maxLength="11" id="phone" placeholder="手机号"/>
								<div className={styles.clear_num}></div>
								</div>
								<div className={loginstyles.textInputBox+' '+loginstyles.Mtop20}>
								<input type="text" className={loginstyles.inputText} maxLength="6" id="code" placeholder="验证码"/>
								<input type="button" className={loginstyles.btnCode} id="sendYzm" value="发送验证码"/>
								</div>
								<div className={loginstyles.Mtop60}>
								<input type="button" className={loginstyles.btnDefault} value="登录" id="loginBut"/>
								<p className="btomjump">没有账号？<a href="javascript:;" id="go_register">立即注册</a></p>
								</div>
								</div>
								</div>
								<div className="swiper-slide">
								<div className="swiper-wrap">
								<div className={loginstyles.textInputBox+' '+loginstyles.Mtop40}>
								<input type="tel" className={loginstyles.inputText} id="phone2" maxLength="11" placeholder="手机号"/>
								<div className="clear_num"></div>
								</div>
								<div className={loginstyles.textInputBox+' '+loginstyles.Mtop20}>
								<input type="password" className={loginstyles.inputText} maxLength="20" id="pwd" placeholder="6～20位密码"/>
								<a href="javascript:;" className="pwd-show"></a>
								<a href="javascript:;" className="pwd-hide"></a>
								<i className="forget">忘记密码?</i>
								</div>
								<div className={loginstyles.btnInputBox+' '+loginstyles.Mtop60}>
								<input type="button" className={loginstyles.btnDefault} value="登录" id="loginBut2"/>
								<p className={loginstyles.btomjump}>没有账号？<a href="javascript:;" id="go_register">立即注册</a></p>
								</div>
								</div>
								</div>
								</Swiper>
								</div>);
							
}});
module.exports = Login;



